import {Component, ComponentInterface, Event, EventEmitter, h, Host, Method, Prop, Watch} from '@stencil/core';
import {OptionHelper} from '../../services/OptionHelper';
import {throttle} from '../../utils/Throttle';
import {isNotEmpty} from '../../utils/utils';
import {SelectHelper} from '../../services/SelectHelper';

@Component({
  tag: 'soul-text-option',
  styleUrl: 'soul-text-option.scss',
  shadow: true
})
export class SoulTextOption implements ComponentInterface {

  @Prop({reflect: true}) public additionalText: string;
  @Prop({reflect: true}) public disabled = false;
  @Prop({reflect: true}) public hidden = false;
  @Prop({reflect: true}) public highlighted = false;
  @Prop({reflect: true}) public passive = false;
  @Prop({reflect: true}) public selected = false;
  @Prop({reflect: true}) public dataSelected = false;
  @Prop({reflect: true}) public text: string;
  @Prop({reflect: true}) public nonFilterable = false;
  @Prop({reflect: false, mutable: false}) public readonly instanceId = `js-soul-text-option-${++textOptionId}`;
  @Prop() public value: any;

  @Event() public soulOptionChange: EventEmitter<SoulTextOption>;
  @Event() public soulOptionClick: EventEmitter<SoulTextOption>;
  @Event() public soulOptionHover: EventEmitter<SoulTextOption>;
  @Event() public soulOptionSelectChange: EventEmitter<SoulTextOption>;

  private iconSlot: HTMLSlotElement;
  private infoSlot: HTMLSlotElement;
  private textOptionElement: HTMLDivElement;
  private optionHelper: OptionHelper;
  private hostElement: HTMLElement;

  public componentDidLoad(): void {
    this.optionHelper = new OptionHelper(this.onChange.bind(this), [this.infoSlot, this.iconSlot]);
    if (this.selected) {
      this.onSelectedChange();
    }
    this.onChange(); // Needed when option has no slotted content
  }

  public componentDidRender(): void {
    this.setIconSlotVisibility();
  }

  public disconnectedCallback() {
    this.optionHelper?.disconnect();
  }

  @Method()
  public async clone(): Promise<HTMLSoulTextOptionElement> {
    const clonedElement = document.createElement('soul-text-option');
    clonedElement.text = this.text;
    clonedElement.additionalText = this.additionalText;
    clonedElement.passive = true;
    const clonedSlotContents: Element[] = this.optionHelper.cloneSlotsContent();
    clonedElement.append(...clonedSlotContents);
    return Promise.resolve(clonedElement);
  }

  @Method()
  public async match(term: string): Promise<boolean> {
    return this.text?.toLowerCase().includes(term.toLowerCase()) || this.additionalText?.toLowerCase().includes(term.toLowerCase());
  }


  @Watch('text')
  @Watch('additionalText')
  public onChange(): void {
    if (this.optionHelper && this.dataSelected && !this.passive) {
      this.soulOptionChange.emit(this);
    }
  }

  @Watch('selected')
  public onSelectedChange(): void {
    if (this.optionHelper && !this.passive) {
      this.dataSelected = this.selected;
      this.optionHelper.onSelectChange(this.selected);
      this.soulOptionSelectChange.emit(this);
    }
  }

  render() {
    return (
      <Host ref={el => this.hostElement = el as HTMLElement}>
        <div class="soul-text-option" onMouseMove={throttle(this.onMouseMove.bind(this), 1000)} onMouseDown={(event) => this.onMouseDown(event)}
             ref={el => this.textOptionElement = el as HTMLDivElement}>
          <div class="soul-grid soul-grid--horizontal soul-grid--align-center soul-grid--compact-gap">
            <div class="soul-grid-item-auto-span  soul-text-option__icon">
              <slot ref={el => this.iconSlot = el as HTMLSlotElement} name="icon" onSlotchange={this.setIconSlotVisibility.bind(this)}></slot>
            </div>
            <div class="soul-grid-item-fill-span">
              {isNotEmpty(this.text) ? <span class="soul-label h-truncate">{this.text}</span> : ''
              }
              {isNotEmpty(this.additionalText) ? <span class="soul-label soul-label--reduced soul-label--micro h-truncate">{this.additionalText}</span> : ''
              }
            </div>
            <div class="soul-grid-item-auto-span">
              <slot ref={el => this.infoSlot = el as HTMLSlotElement} name="info"></slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }

  private onMouseMove(): void {
    if (!this.passive && !this.isOptionDisabled()) {
      this.soulOptionHover.emit(this);
    }
  }

  private onMouseDown(event: MouseEvent): void {
    if (!this.passive) {
      event.preventDefault();
      event.stopPropagation();
      if (!this.isOptionDisabled()) {
        this.soulOptionClick.emit(this);
      }
    }
  }

  private isOptionDisabled(): boolean {
    return this.disabled || this.hostElement.hasAttribute(SelectHelper.GROUP_DISABLED_ATTRIBUTE);
  }

  private setIconSlotVisibility(): void {
    if (this.iconSlot.assignedElements().length > 0) {
      this.textOptionElement.style.setProperty('--soul-text-option-icon-display', 'flex');
    } else {
      this.textOptionElement.style.setProperty('--soul-text-option-icon-display', 'none');
    }
  }

}

let textOptionId = 0;
