@use "modules/components/grid";
@use "modules/components/label";
@use 'modules/essentials/helpers/text';
@use "modules/core";

:host {
  display: block;
  cursor: pointer;
}

:host([highlighted]:not([highlighted="false"])) {
  .soul-text-option {
    background-color: core.$soul-color-background-hover-on-light;
  }
}

:host([data-selected]:not([data-selected="false"])) {
  .soul-text-option {
    background-color: core.$soul-color-background-active;
  }
}

:host([data-filtered]),
:host([data-group-hidden]:not([data-group-hidden="false"])),
:host([hidden]:not([hidden="false"])) {
  display: none;
}
:host([data-group-disabled]:not([data-group-disabled="false"])),
:host([disabled]:not([disabled="false"])) {
  opacity: core.$opacity-l;
  cursor: not-allowed;

  ::slotted(soul-file-icon) {
    opacity: core.$opacity-m;
  }

  ::slotted(soul-icon) {
    --soul-icon-color: #{core.$soul-color-text-on-light};
  }
}

.soul-text-option {
  padding: core.$soul-space-inset-xs;
  border-radius: core.$soul-border-radius-m;
}

.soul-text-option__icon {
  display: var(--soul-text-option-icon-display, none);
}


